import React, { Fragment } from "react";
import BannerTitle from "@Components/layouts/common/BannerTitle/BannerTitle";
import Breadcrumb from "@Components/layouts/common/Breadcrumb/Breadcrumb";
import NavBar from "@Components/layouts/common/NavBar/NavBar";
import MapaSite from "@Components/layouts/common/MapaSite/MapaSite";
import RedesSociais from "@Components/layouts/common/RedesSociais/RedesSociais";
import Footer from "@Components/layouts/common/Footer/Footer";
import { Link } from "react-router-dom";
import PageComponent from "@Components/core/PageComponent";
import style from "../styles.css";
import servicesName from "../services.constant";
import FormOrcamentoServico from "@Components/forms/common/FormOrcamentoServico";
import HotLink from "@Components/layouts/common/HotLink/HotLink";
import SiteSVG from "./SiteSVG";

export default class Site extends PageComponent {
  render() {
    return (
      <Fragment>
        {/* <!-- Configuro landscape --> */}
        <div id="lying" />

        {/* <!-- Configuro natural --> */}
        <div id="standing">
          <div id="wrapper-main" className="page-inside site">
            <NavBar />

            <main>
              <section id="wrapper-banner">
                <BannerTitle>
                  <h1>
                    <strong>Sites</strong>
                  </h1>
                </BannerTitle>
              </section>

              <section id="wrapper-master">
                <Breadcrumb style={style.breadcrumb}>
                  <li>
                    <Link to="/">dgitozero</Link>
                  </li>
                  <li>
                    <Link to="/development">Desenvolvimento</Link>
                  </li>
                  <li>
                    <span>Site</span>
                  </li>
                </Breadcrumb>

                <div style={style.background}>
                  <div className="container" style={style.subContainer}>
                    <SiteSVG />
                    <h2 style={style.mainTitle}>Mais que apenas sites</h2>
                    <p style={style.subTitle}>Websites otimizados para SEO</p>
                    <span style={style.hr} />
                    <div className="row">
                      <div className="col-xl-5 col-lg-5 col-md-12 col-sm-12 col-12">
                        <p style={style.p}>
                        H 20 anos, uma empresa raramente dependia de um site para se manter viva no mercado. Se voltarmos um pouco mais no tempo, sequer existia a prpria internet. Porm, tudo mudou e toda empresa precisa de um site de credibilidade e otimizado para buscadores. Na dgitozero desenvolvemos websites modernos e customizados para todos os segmentos e portes de negcio.
                        </p>
                        <h3 style={style.h3}>Websites otimizados para SEO</h3>
                        <p style={style.p}>
                        Nosso time tem a expertise necessria para desenvolver um site totalmente responsivo para mobile, tablet e tambm alinhados s melhores prticas do Google: o chamado SEO (Search Engine Optimization). Atravs de um bom trabalho de <HotLink to="/inbound-marketing/seo">SEO</HotLink>, seu um site estar otimizado, assim sua empresa tende a ser posicionada entre as mais bem ranqueadas na primeira pgina de resultados de pesquisas do Google, atraindo a ateno e disputando cada clique do seu pblico-alvo com a concorrncia.
                        </p>
                      </div>
                      <div className="col-xl-5 offset-xl-2 col-lg-5 offset-lg-2 col-md-12 col-sm-12 col-12">
                        <h3 style={style.h3}>Por que a dgitozero?</h3>
                        <p style={style.p}>Nossos experientes web designers desenvolvem sites priorizando um atributo de extremamente valorizado na atualidade: a Usabilidade.</p>
                        <p style={style.p}> Entregamos pginas intuitivas, interativas e diretas, focadas na clareza e boa <HotLink to="/inbound-marketing/seo">experincia do usurio (UX)</HotLink>. Cada vez mais, o Google valoriza sites que no confundam o usurio, e sim forneam facilidade, sem exigir que ele tenha que pensar muito. O buscador penaliza pginas que no estejam adaptadas s boas prticas de UX e dificultem a vida de quem navega, por isso  muito importante investir em um bom website para sua empresa. Afinal de contas,  a cara do seu negcio.</p>
                      </div>
                    </div>
                  </div>
                </div>
                <FormOrcamentoServico service={servicesName.DEVELOPMENT} />
              </section>

              <section id="wrapper-map" className="featured">
                <MapaSite />
              </section>

              <section id="wrapper-social-networks">
                <RedesSociais />
              </section>
            </main>

            <footer id="wrapper-footer">
              <Footer />
            </footer>
          </div>
        </div>
      </Fragment>
    );
  }
}



