import React, { Fragment } from "react";
import BannerTitle from "@Components/layouts/common/BannerTitle/BannerTitle";
import Breadcrumb from "@Components/layouts/common/Breadcrumb/Breadcrumb";
import NavBar from "@Components/layouts/common/NavBar/NavBar";
import MapaSite from "@Components/layouts/common/MapaSite/MapaSite";
import RedesSociais from "@Components/layouts/common/RedesSociais/RedesSociais";
import Footer from "@Components/layouts/common/Footer/Footer";
import { Link } from "react-router-dom";
import PageComponent from "@Components/core/PageComponent";
import UXSVG from "./UXSVG";
import style from "../styles.css";
import servicesName from "../services.constant";
import FormOrcamentoServico from "@Components/forms/common/FormOrcamentoServico";

export default class UX extends PageComponent {
  render() {
    return (
      <Fragment>
        {/* <!-- Configuro landscape --> */}
        <div id="lying" />

        {/* <!-- Configuro natural --> */}
        <div id="standing">
          <div id="wrapper-main" className="page-inside usabilidade">
            <NavBar />

            <main>
              <section id="wrapper-banner">
                <BannerTitle>
                  <h1>
                    <strong>UX</strong>
                  </h1>
                </BannerTitle>
              </section>

              <section id="wrapper-master">
                <Breadcrumb style={style.breadcrumb}>
                  <li>
                    <Link to="/">dgitozero</Link>
                  </li>
                  <li>
                    <Link to="/design">Design</Link>
                  </li>
                  <li>
                    <span>UX</span>
                  </li>
                </Breadcrumb>

                <div style={style.background}>
                  <div className="container" style={style.subContainer}>
                    <UXSVG />
                    <h2 style={style.mainTitle}>A experincia do usurio</h2>
                    <p style={style.subTitle}>Conjunto de aspectos referentes  interao do usurio</p>
                    <span style={style.hr} />
                    <div className="row">
                      <div className="col-xl-5 col-lg-5 col-md-12 col-sm-12 col-12">
                        <h3 style={style.h3}>Experincia de Usurio</h3>
                        <p style={style.p}>
                          A dgitozero conta com um time de desenvolvedores experiente e treinado nas principais tecnologias que o mercado oferece. Somos especialistas em desenvolvimento web, criamos sites, aplicativos e lojas online em diversas linguagens e para diversas plataformas.
                        </p>
                        <h3 style={style.h3}>O Google e a Valorizao da UX</h3>
                        <p style={style.p}>
                          Voc j entrou em um site disposto a comprar algo e
                          desistiu aps perceber que as pginas eram mal
                          pensadas, difceis de entender e com design ruim?
                          Alguma das 3 opes? Todas juntas? Pois , a grande
                          maioria das pessoas tende a desistir rapidamente se
                          percebem que um site no  bem como elas esperavam.
                          Estamos nos acostumando cada vez mais com agilidade e
                          facilidade, e cada vez menos temos pacincia para
                          sites que no funcionam bem. Da toda a importncia
                          dessa cincia moderna, chamada de User Experience.
                        </p>
                      </div>
                      <div className="col-xl-5 offset-xl-2 col-lg-5 offset-lg-2 col-md-12 col-sm-12 col-12">
                        <p style={style.p}>
                          UX est relacionada ao design, que  responsvel por
                          desenvolver a interface de forma que favorea a
                          usabilidade e mxima facilidade de entendimento do
                          usurio enquanto navega. Contudo, UX no se resume
                          apenas a isso. Trata-se de uma cincia dinmica e
                          bastante subjetiva, que depende da percepo
                          individual do usurio e tende a mudar de tempos em
                          tempos, devido  constantes inovaes tecnolgicas e
                          de padres de uso da internet.
                        </p>
                        <p style={style.p}>
                          O algoritmo do Google est sempre evoluindo e j
                          consegue reconhecer muitos padres de UX, valorizando
                          quem os segue e penalizando aqueles que no investem
                          em usabilidade. O resultado disso impacta diretamente
                          no SEO.
                        </p>
                        <p style={style.p}>
                          A dgitozero conta com um time de design com bastante
                          experincia em projetos de usabilidade e antenado nas
                          melhores prticas adotadas pelo mercado atualmente.
                        </p>
                      </div>
                    </div>
                  </div>
                </div>
                <FormOrcamentoServico service={servicesName.UX} />
              </section>

              <section id="wrapper-map" className="featured">
                <MapaSite />
              </section>

              <section id="wrapper-social-networks">
                <RedesSociais />
              </section>
            </main>

            <footer id="wrapper-footer">
              <Footer />
            </footer>
          </div>
        </div>
      </Fragment>
    );
  }
}



