import React, { Fragment } from "react";
import BannerTitle from "@Components/layouts/common/BannerTitle/BannerTitle";
import Breadcrumb from "@Components/layouts/common/Breadcrumb/Breadcrumb";
import NavBar from "@Components/layouts/common/NavBar/NavBar";
import MapaSite from "@Components/layouts/common/MapaSite/MapaSite";
import RedesSociais from "@Components/layouts/common/RedesSociais/RedesSociais";
import Footer from "@Components/layouts/common/Footer/Footer";
import { Link } from "react-router-dom";
import PageComponent from "@Components/core/PageComponent";
import style from "../styles.css";
import FormOrcamentoServico from "@Components/forms/common/FormOrcamentoServico";
import HotLink from "@Components/layouts/common/HotLink/HotLink";
import servicesName from "../services.constant";
import EmailMarketingSVG from "./EmailMarketingSVG";

export default class EmailMarketing extends PageComponent {
  render() {
    return (
      <Fragment>
        {/* <!-- Configuro landscape --> */}
        <div id="lying" />

        {/* <!-- Configuro natural --> */}
        <div id="standing">
          <div id="wrapper-main" className="page-inside email-marketing">
            <NavBar />

            <main>
              <section id="wrapper-banner">
                <BannerTitle>
                  <h1>
                    <strong>E-mail Marketing</strong>
                  </h1>
                </BannerTitle>
              </section>

              <section id="wrapper-master">
                <Breadcrumb style={style.breadcrumb}>
                  <li>
                    <Link to="/">dgitozero</Link>
                  </li>
                  <li>
                    <Link to="/inbound">Inbound</Link>
                  </li>
                  <li>
                    <span>Marketing de Contedo</span>
                  </li>
                </Breadcrumb>

                <div style={style.background}>
                  <div className="container" style={style.subContainer}>
                    <EmailMarketingSVG />
                    <h2 style={style.mainTitle}>
                      Um grande ferramenta assertiva
                    </h2>
                    <p style={style.subTitle}>
                      A comunicao direta entre sua empresa e seu pblico-alvo
                      via e-mail
                    </p>
                    <span style={style.hr} />
                    <div className="row">
                      <div className="col-xl-5 col-lg-5 col-md-12 col-sm-12 col-12">
                        <h3 style={style.h3}>O que  E-mail Marketing?</h3>
                        <p style={style.p}>
                          O E-mail Marketing nada mais  que a comunicao
                          direta entre sua empresa e seu pblico-alvo via
                          e-mail. Trata-se de uma das ferramentas mais
                          assertivas de marketing direto se for bem integrada 
                          estratgia de Inbound Marketing, sendo importantssima
                          na chamada nutrio da relao com os potenciais
                          clientes da empresa.
                        </p>
                        <h3 style={style.h3}>
                          Uma Poderosa Ferramenta de Comunicao Direta
                        </h3>
                        <p style={style.p}>
                          Mais de 90% dos adultos fazem uso do servio de
                          e-mails. Voc com certeza j recebeu na sua caixa de
                          entrada alguma promoo, newsletter e afins, que so
                          exemplos bastante comuns da prtica de propagandas
                          tipo mala direta. H alguns anos atrs, era muito
                          comum que o E-mail marketing fosse usado apenas desta
                          forma, ganhando uma conotao negativa de spam
                          ineficiente. Porm, com o desenvolvimento do Marketing
                          Digital que culminou no nascimento da estratgia de
                          Inbound Marketing, enviar e-mails para o cliente
                          assumiu um papel mais importante.
                        </p>
                      </div>
                      <div className="col-xl-5 offset-xl-2 col-lg-5 offset-lg-2 col-md-12 col-sm-12 col-12">
                        <p style={style.p}>
                          Ao contrrio do que muitos pensam, o E-mail Marketing
                           hoje uma das ferramentas mais assertivas e
                          bem-sucedidas dentro de uma estratgia automatizada de
                          marketing, uma das formas mais pessoais de estabelecer
                          contato com seus consumidores. Isto porque atualmente
                           possvel segmentar os destinatrios de forma
                          inteligente, para que envie o e-mail certo para o
                          indivduo que provavelmente ter interesse no contedo
                          da mensagem.
                        </p>
                        <h3 style={style.h3}>
                          Qual o papel do e-mail marketing na sua estratgia de
                          marketing?
                        </h3>
                        <ol>
                          <li>Engajar os leads</li>
                          <li>Promover seus contedos</li>
                          <li>Nutrir e educar seus leads</li>
                          <li>Vender</li>
                          <li>Relao ps-venda</li>
                        </ol>
                        <h3 style={style.h3}>
                          Os 4 tipos de email que sua estratgia precisa ter
                        </h3>
                        <ol>
                          <li>Atualizaes do Blog</li>
                          <li>Newsletter</li>
                          <li>Convites para eventos</li>
                          <li>Emails de nutrio de leads</li>
                        </ol>
                      </div>
                    </div>
                  </div>
                </div>
                <FormOrcamentoServico service={servicesName.INBOUND} />
              </section>

              <section id="wrapper-map" className="featured">
                <MapaSite />
              </section>

              <section id="wrapper-social-networks">
                <RedesSociais />
              </section>
            </main>

            <footer id="wrapper-footer">
              <Footer />
            </footer>
          </div>
        </div>
      </Fragment>
    );
  }
}



